<template>
  <div id="index">
    <nut-navbar class="nav" :left-show="false" title="西四庄村来返人员报备" fixed>
      <template #left>
        <nut-icon font-class-name="my-icon" class-prefix="my-icon" name="extra" />
      </template>
    </nut-navbar>
    <nut-icon font-class-name="iconfont" class-prefix="icon" name="icon-niu" />
    <nut-form>
      <nut-input
        label="姓名"
        placeholder="请输入姓名"
        type="text"
        v-model="info.name"
        clearable
        @update:model-value="changeResultName"
      />
      <nut-input
        label="身份证号"
        placeholder="请输入身份证号"
        type="digit"
        v-model="info.idcard"
        clearable
        @update:model-value="changeResultIdcard"
      />
      <nut-input
        label="联系方式"
        placeholder="请输入联系方式"
        type="digit"
        v-model="info.tel"
        clearable
        @update:model-value="changeResultTel"
      />
      <nut-input
        label="家庭地址"
        class="nut-input-text"
        placeholder="输入格式 ***村*区*排*号"
        type="text"
        v-model="info.address"
        clearable
        @update:model-value="changeResultAddress"
      />
      <nut-input
        label="来源地"
        placeholder="输入格式 **省**市**区**街道/镇"
        type="text"
        v-model="info.srcaddr"
        clearable
        @update:model-value="changeResultSrcaddr"
      />
      <nut-cell
        title="返回日期"
        class="date"
        :desc="desc"
        @click="show = true"
      ></nut-cell>
      <nut-datepicker
        v-model="currentDate"
        v-model:visible="show"
        :min-date="minDate"
        :max-date="maxDate"
        :is-show-chinese="true"
        @confirm="confirm"
      ></nut-datepicker>

      <nut-cell-group title="48小时内核酸证明">
        <nut-cell>
          <nut-radiogroup
            v-model="info.prove48h"
            direction="horizontal"
            @change="changeResultProve48h"
          >
            <nut-radio label="阴性">阴性</nut-radio>
            <nut-radio label="未检测">未检测</nut-radio>
            <nut-radio label="未出结果">未出结果</nut-radio>
            <nut-radio label="阳性">阳性</nut-radio>
          </nut-radiogroup>
        </nut-cell>
      </nut-cell-group>

      <nut-form-item label="备注信息">
        <nut-textarea
          placeholder="请输入备注"
          type="text"
          rows="4"
          :value="info.others"
          @change="changeResultOthers"
        />
      </nut-form-item>
      <nut-button size="large" type="info" shape="square" @click="state.showBasic = true"
        >确认报备</nut-button
      >
      <p></p>
      <nut-button size="large" type="primary" shape="square" @click="clearResults"
        >清空信息</nut-button
      >
    </nut-form>
    <nut-popup
      pop-class="resultPop"
      :style="{ padding: '50px 30px' }"
      v-model:visible="state.showBasic"
      :z-index="100"
      :close-on-click-overlay="false"
      closeable
      round
    >
      <nut-textarea
        v-model="results"
        rows="10"
        readonly
        placeholder=" "
        :style="popsize"
      ></nut-textarea>

      <div align="center">
        <!-- // <qrcode-vue :value="results" :size="200" level="H" foreground="#0059ff" /> -->
        <qrcode-vue :value="results" :size="200" level="H" />
        <div class="tip">①请将此二维码展示给村工作人员</div>
        <div class="tip">②或将报备信息发送给村工作人员</div>
      </div>
      <p></p>
      <nut-button type="info" size="large" @click="copyResults">复制报备信息</nut-button>
    </nut-popup>
  </div>
</template>

<script scope>
import { reactive, ref } from "vue";
import useClipboard from "vue-clipboard3";
import QrcodeVue from "qrcode.vue";
import { Toast  } from "@nutui/nutui";
export default {
  name: "IndexView",
  props: {},
  data() {
    return {};
  },
  components: {
    QrcodeVue,
  },

  setup() {
    let year = new Date().getFullYear();
    let month = new Date().getMonth() + 1;
    let day = new Date().getDate();

    const show = ref(false);
    const desc = ref(year + "年" + month + "月" + day + "日");

    const minDate = new Date(2022, 1, 1);
    const maxDate = new Date(2026, 12, 1);
    const currentDate = new Date();

    const state = reactive({
      showBasic: false,
    });

    let info = reactive({
      name: "",
      idcard: "",
      tel: "",
      address: "",
      srcaddr: "",
      prove48h: ref("阴性"),
      others: "",
    });

    const clearResults = () => {
      desc.value = year + "年" + month + "月" + day + "日";

      info.name = "";
      info.idcard = "";
      info.tel = "";
      info.address = "";
      info.srcaddr = "";
      info.prove48h = ref("阴性");
      info.others = "";
    };

    const results = ref(
      `姓名：${info.name}\n身份证号：${info.idcard}\n电话：${info.tel}\n家庭地址：${info.address}\n来源地：${info.srcaddr}\n48小时核酸：${info.prove48h}\n返回日期：${desc.value}\n备注：${info.others}`
    );
    const changeResultName = (val) => {
      results.value = `姓名：${val}\n身份证号：${info.idcard}\n电话：${info.tel}\n家庭地址：${info.address}\n来源地：${info.srcaddr}\n48小时核酸：${info.prove48h}\n返回日期：${desc.value}\n备注：${info.others}`;
    };
    const changeResultIdcard = (val) => {
      results.value = `姓名：${info.name}\n身份证号：${val}\n电话：${info.tel}\n家庭地址：${info.address}\n来源地：${info.srcaddr}\n48小时核酸：${info.prove48h}\n返回日期：${desc.value}\n备注：${info.others}`;
    };
    const changeResultTel = (val) => {
      results.value = `姓名：${info.name}\n身份证号：${info.idcard}\n电话：${val}\n家庭地址：${info.address}\n来源地：${info.srcaddr}\n48小时核酸：${info.prove48h}\n返回日期：${desc.value}\n备注：${info.others}`;
    };

    const changeResultAddress = (val) => {
      results.value = `姓名：${info.name}\n身份证号：${info.idcard}\n电话：${info.tel}\n家庭地址：${val}\n来源地：${info.srcaddr}\n48小时核酸：${info.prove48h}\n返回日期：${desc.value}\n备注：${info.others}`;
    };

    const changeResultSrcaddr = (val) => {
      results.value = `姓名：${info.name}\n身份证号：${info.idcard}\n电话：${info.tel}\n家庭地址：${info.address}\n来源地：${val}\n48小时核酸：${info.prove48h}\n返回日期：${desc.value}\n备注：${info.others}`;
    };

    const changeResultProve48h = (leb) => {
      results.value = `姓名：${info.name}\n身份证号：${info.idcard}\n电话：${info.tel}\n家庭地址：${info.address}\n来源地：${info.srcaddr}\n48小时核酸：${leb}\n返回日期：${desc.value}\n备注：${info.others}`;
    };
    const confirm = ({ selectedValue, selectedOptions }) => {
      desc.value = selectedOptions.map((option) => option.text).join("");
      results.value = `姓名：${info.name}\n身份证号：${info.idcard}\n电话：${info.tel}\n家庭地址：${info.address}\n来源地：${info.srcaddr}\n48小时核酸：${info.prove48h}\n返回日期：${desc.value}\n备注：${info.others}`;
      console.log(selectedValue);
    };

    const changeResultOthers = (val) => {
      results.value = `姓名：${info.name}\n身份证号：${info.idcard}\n电话：${info.tel}\n家庭地址：${info.address}\n来源地：${info.srcaddr}\n48小时核酸：${info.prove48h}\n返回日期：${desc.value}\n备注：${val}`;
    };

    const screenWidth = document.body.clientWidth; // 屏幕宽
    // const  screeHeight= document.body.clientHeight;    // 屏幕高
    let popsize = `width:${screenWidth * 0.8}px`;

    const { toClipboard } = useClipboard();

    const copyResults = async () => {
      try {
        await toClipboard(results.value);
        Toast.success("复制成功", { duration: 2000 });
      } catch (e) {
        Toast.fail("复制失败请手动操作", { duration: 2000 });
      }
    };

    return {
      popsize,

      show,
      desc,
      minDate,
      maxDate,
      currentDate,

      state,
      info,
      clearResults,
      results,
      changeResultName,
      changeResultIdcard,
      changeResultTel,
      changeResultAddress,
      changeResultSrcaddr,
      changeResultProve48h,
      confirm,
      changeResultOthers,
      copyResults,
    };
  },
  mounted() {},
};
</script>

<style>
.tip{
  color:#FA5419;
  font-weight: bold;
}

.date {
  text-align: left;
}

#app {
  height: auto !important;
}

.nav {
  font-size: 20px;
}
.resultPop {
  color: #d01a1a;
}

@font-face {
  font-family: "my-icon";
  src: url("../assets/font/iconfont.ttf") format("truetype");
}

.my-icon {
  font-family: "my-icon";
  font-size: 24px;
  color: rgb(78, 77, 77);
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.my-icon-extra::before {
  content: "\e697";
}
</style>
